          <h2 id="vue-doc-自定义上传列表1" class="vue-doc-element">自定义上传列表</h2>
<p class="vue-doc-element">可以自由控制上传列表，完成各种业务逻辑，示例是一个照片墙，可以查看大图和删除。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-iconshapes1" class="vue-doc-element">icon&amp;shapes</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <ul class="vue-doc-element">
<li>设置属性 show-upload-list 为 false，可以不显示默认的上传列表。</li>
<li>设置属性 default-file-list 设置默认已上传的列表。</li>
<li>通过 on-success 等属性来控制完整的上传过程，详见API。
另外，可以通过丰富的配置，来定制上传需求，本例中包含了：</li>
<li>文件必须是 jpg 或 png 格式的图片。</li>
<li>最多上传5张图片。</li>
<li>每个文件大小不超过 2M。</li>
</ul>

