<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-点击上传1">点击上传</a></li><li><a href="#vue-doc-多选1">多选</a></li><li><a href="#vue-doc-手动上传1">手动上传</a></li><li><a href="#vue-doc-拖拽上传1">拖拽上传</a></li><li><a href="#vue-doc-自定义上传列表1">自定义上传列表</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-upload上传1" class="vue-doc-element">Upload 上传</h1>
<p class="vue-doc-element">文件选择上传和拖拽上传控件。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demomultiple></demomultiple>
<demohandleUpload></demohandleUpload>
<demodrag></demodrag>
<democustom></democustom>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-uploadprops1_1" class="vue-doc-element">Upload props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>action</td>
<td>上传的地址，必填</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>headers</td>
<td>设置上传的请求头部</td>
<td>Object</td>
<td>{}</td>
</tr>
<tr>
<td>multiple</td>
<td>是否支持多选文件</td>
<td>Boolean</td>
<td>FALSE</td>
</tr>
<tr>
<td>data</td>
<td>上传时附带的额外参数</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>上传的文件字段名</td>
<td>String</td>
<td>file</td>
</tr>
<tr>
<td>with-credentials</td>
<td>支持发送 cookie 凭证信息</td>
<td>Boolean</td>
<td>FALSE</td>
</tr>
<tr>
<td>show-upload-list</td>
<td>是否显示已上传文件列表</td>
<td>Boolean</td>
<td>TRUE</td>
</tr>
<tr>
<td>type</td>
<td>上传控件的类型，可选值为 select（点击选择），drag（支持拖拽）</td>
<td>String</td>
<td>select</td>
</tr>
<tr>
<td>accept</td>
<td>接受上传的文件类型</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>format</td>
<td>支持的文件类型，与 accept 不同的是，format 是识别文件的后缀名，accept 为 input 标签原生的 accept 属性，会在选择文件时过滤，可以两者结合使用</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>max-size</td>
<td>文件大小限制，单位 kb</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>before-upload</td>
<td>上传文件之前的钩子，参数为上传的文件，若返回 false 或者 Promise 则停止上传</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-progress</td>
<td>文件上传时的钩子，返回字段为 event, file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-success</td>
<td>文件上传成功时的钩子，返回字段为 response, file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-error</td>
<td>文件上传失败时的钩子，返回字段为 error, file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-preview</td>
<td>点击已上传的文件链接时的钩子，返回字段为 file， 可以通过 file.response 拿到服务端返回数据</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-remove</td>
<td>文件列表移除文件时的钩子，返回字段为 file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-format-error</td>
<td>文件格式验证失败时的钩子，返回字段为 file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>on-exceeded-size</td>
<td>文件超出指定大小限制时的钩子，返回字段为 file, fileList</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>default-file-list</td>
<td>默认已上传的文件列表，例如：</td>
<td>Array</td>
<td>[]</td>
</tr>
</tbody>
</table>
<pre class="vue-doc-element"><code>默认已上传的文件列表，例如：

[
    {
        name: <span class="hljs-symbol">'img1</span>.jpg',
        url: <span class="hljs-symbol">'http</span>:<span class="hljs-comment">//www.xxx.com/img1.jpg'</span>
    },
    {
        name: <span class="hljs-symbol">'img2</span>.jpg',
        url: <span class="hljs-symbol">'http</span>:<span class="hljs-comment">//www.xxx.com/img2.jpg'</span>
    }
]

</code></pre><h3 id="vue-doc-uploadmethods1_2" class="vue-doc-element">Upload methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>clearFiles</td>
<td>清空已上传的文件列表</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-uploadslot1_3" class="vue-doc-element">Upload slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>触发上传组件的控件</td>
</tr>
<tr>
<td>tip</td>
<td>辅助提示内容</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demomultiple from './demo/multiple.md';
import demohandleUpload from './demo/handleUpload.md';
import demodrag from './demo/drag.md';
import democustom from './demo/custom.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demomultiple,demohandleUpload,demodrag,democustom
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


